<template>
  <div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-nav-bar title="表白墙" left-text="返回" left-arrow @click-left="onClickLeft" fixed />

      <p>刷新次数: {{ count }}</p>

      <div id="love-content" style="margin-top:46px">
        <van-dropdown-menu>
          <van-dropdown-item v-model="value1" :options="option1" />
          <van-dropdown-item v-model="value2" :options="option2" />
        </van-dropdown-menu>
        <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <!-- <van-cell v-for="item in list" :key="item" :title="item"> -->
          <div v-for="item in list" :key="item" :title="item">
            <van-panel icon="https://img.yzcdn.cn/vant/cat.jpeg" title="标题" desc="描述信息" status="状态">
              <router-link to="/love">
              <div>
               
                    <div>
                      Vue (读音 /vjuː/，类似于 view) 是一套用于构建用户界面的渐进式框架。与其它大型框架不同的是，
                      Vue 被设计为可以自底向上逐层应用。Vue 的核心库只关注视图层，不仅易于上手，还便于与第三方库或既有项目整合。另一方面，当与
                      现代化的工具链以及各种支持类库结合使用时，Vue 也完全能够为复杂的单页应用提供驱动。 {{item}}
                    </div>
                  
              </div>
              </router-link>
              <div slot="footer">
                222
                <!-- <van-button size="small">按钮</van-button>
                <van-button size="small" type="danger">按钮</van-button>-->
              </div>
              <div slot="footer">
                111111
                
                <!-- <van-button size="small">按钮</van-button>
                <van-button size="small" type="danger">按钮</van-button>-->
              </div>
            </van-panel>
            <van-divider
              hairline
              :style="{ color: '#1989fa', borderColor: '#1989fa', padding: '0 16px' }"
            >分割线</van-divider>
          </div>
          <!-- </van-cell> -->
        </van-list>
      </div>
    </van-pull-refresh>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value: 0,
      count: 0,
      isLoading: false,
      value1: 0,
      value2: "a",
      option1: [
        { text: "全部商品", value: 0 },
        { text: "新款商品", value: 1 },
        { text: "活动商品", value: 2 }
      ],
      option2: [
        { text: "默认排序", value: "a" },
        { text: "好评排序", value: "b" },
        { text: "销量排序", value: "c" }
      ],
      list: [],
      loading: false,
      finished: false
    };
  },
  methods: {
    onClickLeft() {
      //   Toast("返回");
      this.$router.go(-1);
    },
    onRefresh() {
      setTimeout(() => {
        this.$toast("刷新成功");
        this.isLoading = false;
        this.count++;
      }, 500);
    },
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 500);
    }
  }
};
</script>